import React, { useState, useEffect } from "react";
import SearchBar from "../components/SearchBar";
import Pagination from "../components/Pagination";
import { fetchApi } from "../service/api";
import "../styles.css";
import { motion, AnimatePresence, useAnimation } from "framer-motion";
import astronautImg from "../assets/astronaut.png";
import logoIcon from "../assets/logo.png"; // Importe sua logo aqui
import ParticlesBackground from "../components/ParticlesBackground";

function Home() {
  const [query, setQuery] = useState("");
  const [resultsVisible, setResultsVisible] = useState(false);
  const [results, setResults] = useState([]);
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const itemsPerPage = 10;
  const astronautControls = useAnimation();

  const handleSearch = (newQuery) => {
    if (newQuery.trim()) {
      setQuery(newQuery);
      setPage(1);
      setResultsVisible(true);
    }
  };

  useEffect(() => {
    if (query) {
      loadResults(query, page);
    }
  }, [query, page]);

  useEffect(() => {
    if (resultsVisible) {
      astronautControls
        .start({
          x: "-0vw",
          y: "-70vh",
          rotate: -360,
          transition: {
            duration: 1.5,
            ease: "easeOut",
          },
        })
        .then(() => {
          const moveAstronaut = () => {
            astronautControls.start({
              x: `${Math.random() * 200 - 100}vw`,
              y: `${Math.random() * 200 - 100}vh`,
              rotate: Math.random() * 360 - 180,
              transition: {
                duration: 4,
                ease: "easeInOut",
              },
            });
          };

          moveAstronaut();
          const interval = setInterval(moveAstronaut, 4000);
          return () => clearInterval(interval);
        });
    } else {
      astronautControls.start({
        x: 0,
        y: [0, -20, 20, 0],
        rotate: [0, 2, -2, 0],
        transition: {
          duration: 6,
          ease: "easeInOut",
          repeat: Infinity,
        },
      });
    }
  }, [resultsVisible, astronautControls]);

  const loadResults = async (q, p) => {
    setLoading(true);
    setError(null);
    try {
      const data = await fetchApi(q, p);
      const total = data.total || 0;
      if (total === 0) {
        setResults([]);
        setTotalPages(1);
        setError(
          `Nenhum resultado encontrado para '${q}'. Tente outra palavra!`
        );
      } else {
        setResults(data.Hits || []);
        setTotalPages(Math.ceil(total / itemsPerPage));
      }
    } catch (err) {
      setError(
        "Erro ao buscar os resultados. Por favor, tente novamente mais tarde."
      );
      setResults([]);
      setTotalPages(1);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="home-container starfield">
      <ParticlesBackground />
      {/* restante do seu conteúdo */}

      <motion.img
        src={astronautImg}
        alt="Astronauta flutuando"
        className="floating-astronaut"
        animate={astronautControls}
      />

      <motion.div className="title-and-search">
        <motion.div
          initial={{ opacity: 1, y: 0 }}
          animate={{
            opacity: resultsVisible ? 0 : 1,
            y: resultsVisible ? -300 : 0,
          }}
          transition={{ type: "spring", stiffness: 80 }}
        >
          <img
            src={logoIcon}
            alt="DeepSpace Logo"
            style={{ width: "400px", height: "auto" }}
          />
        </motion.div>

        <motion.div
          className={`search-wrapper ${
            resultsVisible ? "near-results" : "centered"
          }`}
          animate={{ y: resultsVisible ? -20 : 0 }}
          transition={{ type: "spring", stiffness: 80 }}
        >
          <SearchBar onSearch={handleSearch} />
        </motion.div>
      </motion.div>

      <AnimatePresence>
        {resultsVisible && (
          <motion.div
            className="results-wrapper"
            initial={{ opacity: 0, y: 100 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0, y: 100 }}
            transition={{ duration: 0.6 }}
          >
            <div className="results-container">
              <h2>
                Resultados para: <span>{query}</span>
              </h2>

              {loading && (
                <div className="loader-container">
                  <div className="loader" />
                  <p>Carregando...</p>
                </div>
              )}

              {error && (
                <div className="error-message">
                  <p>{error}</p>
                </div>
              )}

              {!loading && !error && (
                <>
                  <div className="results-list">
                    {results.map((item, index) => (
                      <div key={index} className="result-card">
                        <div>
                          <h3>{item.title}</h3>
                          <p
                            dangerouslySetInnerHTML={{
                              __html: highlightTerm(item.abs, query),
                            }}
                          />
                        </div>
                        <a
                          href={item.url}
                          target="_blank"
                          rel="noopener noreferrer"
                          className="view-more"
                        >
                          Ver mais <span className="external-icon">↗</span>
                        </a>
                      </div>
                    ))}
                  </div>

                  <div className="pagination-wrapper">
                    <Pagination
                      page={page}
                      setPage={setPage}
                      totalPages={totalPages}
                      query={query}
                    />
                  </div>
                </>
              )}
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
}

function highlightTerm(text, term) {
  const regex = new RegExp(`(${term})`, "gi");
  return text.replace(regex, "<mark>$1</mark>");
}

export default Home;
