import React, { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { fetchApi } from "../service/api";
import "../styles.css";
import Pagination from "../components/Pagination";

export default function Results() {
  const [results, setResults] = useState([]);
  const [page, setPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [query, setQuery] = useState("");
  const [loading, setLoading] = useState(false); // NEW
  const [error, setError] = useState(null); // NEW
  const location = useLocation();
  const itemsPerPage = 10;

  useEffect(() => {
    const params = new URLSearchParams(location.search);
    const q = params.get("query") || "";
    setQuery(q);
    setPage(1);
  }, [location.search]);

  useEffect(() => {
    if (query) {
      loadResults(query, page);
    }
  }, [query, page]);

  const loadResults = async (q, p) => {
    setLoading(true);
    setError(null);
    try {
      const data = await fetchApi(q, p);
      const total = data.total || 0;
      if (total === 0) {
        setResults([]);
        setTotalPages(1);
        setError(
          `Nenhum resultado encontrado para '${q}'. Tente outra palavra!`
        );
      } else {
        setResults(data.Hits || []);
        setTotalPages(Math.ceil(total / itemsPerPage));
      }
    } catch (err) {
      setError(
        "Erro ao buscar os resultados. Por favor, tente novamente mais tarde."
      );
      setResults([]);
      setTotalPages(1);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="results-container">
      <h2>
        Resultados para: <span>{query}</span>
      </h2>

      {loading && (
        <div className="loader-container">
          <div className="loader"></div>
          <p>Carregando...</p>
        </div>
      )}

      {error && (
        <div className="error-message">
          <p>{error}</p>
        </div>
      )}

      {!loading && !error && (
        <>
          <div className="results-list">
            {results.map((item, index) => (
              <div key={index} className="result-card">
                <div>
                  <h3>{item.title}</h3>
                  <p
                    dangerouslySetInnerHTML={{
                      __html: highlightTerm(item.abs, query),
                    }}
                  ></p>
                </div>
                <a
                  href={item.url}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="view-more"
                >
                  Ver mais
                </a>
              </div>
            ))}
          </div>

          <Pagination
            page={page}
            setPage={setPage}
            totalPages={totalPages}
            query={query}
          />
        </>
      )}
    </div>
  );
}

function highlightTerm(text, term) {
  const regex = new RegExp(`(${term})`, "gi");
  return text.replace(regex, "<mark>$1</mark>");
}
