import React, { useState } from "react";
import "../styles.css";
import { motion } from "framer-motion";

export default function SearchBar({ onSearch }) {
  const [input, setInput] = useState("");
  const [clicked, setClicked] = useState(false);

  const handleSearch = (e) => {
    e.preventDefault();
    if (input.trim()) {
      setClicked(true);
      onSearch(input);
      setTimeout(() => setClicked(false), 400); // reset feedback
    }
  };

  return (
    <motion.div
      className="fullscreen-search"
      animate={
        clicked ? { scale: [1, 1.05, 1], boxShadow: "0 0 12px white" } : {}
      }
      transition={{ duration: 0.3 }}
    >
      <form className="search-bar" onSubmit={handleSearch}>
        <input
          type="text"
          placeholder="Search"
          value={input}
          onChange={(e) => setInput(e.target.value)}
        />
        <button type="submit" aria-label="Search">
          <motion.svg
            whileTap={{ rotate: 20 }}
            xmlns="http://www.w3.org/2000/svg"
            width="20"
            height="20  "
            fill="white"
            viewBox="0 0 24 24"
          >
            <path d="M10 2a8 8 0 105.293 14.293l5.707 5.707 1.414-1.414-5.707-5.707A8 8 0 0010 2zm0 2a6 6 0 110 12A6 6 0 0110 4z" />
          </motion.svg>
        </button>
      </form>
    </motion.div>
  );
}
